/*
 * 
 * */
function ajax(url, data = {}, method = 'GET', h) {
  // TODO URL

  let header = {};

  h && (header['token'] = getApp().globalData.userToken)

  header['Content-Type'] = 'application/x-www-form-urlencoded'
  let baseURL;




  baseURL = 'https://api.plg.wugee.net'

  return new Promise((resolve, reject) => {
    uni.request({
      url: baseURL + url,
      data,
      method:method.toUpperCase(),
      header,
      success(result) {
		  if(result.statusCode == 200){
			  if (result.data.code == 1001 && result.data.msg == 'Token Error'){
				  // TODO token
				  tokenError(h, result, reject)
				  return;
			  }
			  resolve(result.data)
		  }else{
			  reject(result)
		  }
         
        },
      fail(err) {
        console.log('', err.data);
        reject(err)
      }
    })
  })
}

//tokentokentoken
function tokenError(h, result, reject) {
  let token = uni.getStorageSync('token')
  console.log(token);
  if (token && (h && result.data.msg === 'Token Error')) {
    uni.showModal({
      title: '!\n',
      success(res) {
        //
        if (res.confirm) {
          uni.navigateTo({
            url: '/pages/login/login',
          })
          return;
        }
        //
        reject(result)
      },
    })
  } else {
    //
    reject(result)
  }
}

module.exports = ajax
